.maske {
  position: absolute;
  z-index: 2000;
  overflow: hidden;
  width: 100%;
  height: 30%;
  background: #000;
  mix-blend-mode: darken;
}

.maske__inner {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 140vw;
  height: 50vh;
  transform: translate(-50%, -50%) scale(0.01);
  border-radius: 50%;
  background: #fff;
  animation-name: slidein;
  animation-duration: 3s;
  animation-fill-mode: forwards;
}

@keyframes slidein {
  0% {
    transform: translate(-50%, -50%) scale(0.01);
  }
  100% {
    transform: translate(-50%, -50%) scale(1);
  }
}

